@import '~@yandex-music-int/sol/mixins/fontStyles.scss';
@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/firefoxStyles.scss';

.root {
    padding: 0;
    padding-inline-start: var(--ym-spacer-size-xxxl);
    padding-inline-end: var(--ym-spacer-size-xs);

    @include firefoxStyles() {
        padding-inline-end: var(--ym-spacer-size-xxxl);
    }

    @include mediaBreakpointDown(1240, false) {
        padding-inline-end: 0;
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-inline-start: var(--ym-spacer-size-l);
    }
}

.conteiner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    width: 100%;
    height: 100%;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.icon {
    margin-block-start: calc(2 * var(--ym-spacer-size-xxxxl));
    margin-block-end: var(--ym-spacer-size-xxxl);
    padding: var(--ym-spacer-size-m);
}

.title {
    margin-block-end: var(--ym-spacer-size-xs);

    color: var(--ym-controls-color-primary-text-enabled_variant);
}

.subtitle {
    margin-block-end: var(--ym-spacer-size-xxxxl);

    color: var(--ym-controls-color-primary-text-enabled);
}

.footer {
    padding-block-start: var(--ym-spacer-size-xxxxl);
    padding-block-end: var(--ym-spacer-size-xxl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-block-end: var(--ym-spacer-size-xxxl);
    }
}
