@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    --search-header-height-with-filters: 146px;
    --search-header-height-with-filters-mobile: 140px;
    --search-header-height-with-without-filters: 110px;

    padding: 0;
    padding-block-start: var(--search-header-height-with-without-filters);

    width: 100%;
    height: 100%;

    &_showFilters {
        padding-block-start: var(--search-header-height-with-filters);

        @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
            padding-block-start: var(--search-header-height-with-filters-mobile);
        }
    }
}

.header {
    display: flex;

    position: absolute;
    z-index: var(--ym-z-index-header);
    flex-direction: column;

    padding-block-start: var(--ym-spacer-size-xxl);
    padding-inline-start: var(--ym-spacer-size-xxxl);
    padding-inline-end: var(--ym-spacer-size-xxxl);
    inset-block-start: 0;
    inset-inline-end: 0;
    inset-inline-start: 0;
    gap: var(--ym-spacer-size-m);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        gap: var(--ym-spacer-size-xs);

        padding-inline-start: var(--ym-spacer-size-l);
        padding-inline-end: var(--ym-spacer-size-l);
    }
}

.content {
    width: 100%;
    height: 100%;
}

.main {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    height: 100%;
}

.tabCarousel {
    margin-inline-start: calc(-1 * var(--ym-spacer-size-xxxs));
    padding-block-start: var(--ym-spacer-size-xxxs);
    padding-inline-start: var(--ym-spacer-size-xxxs);
    padding-inline-end: 0;
    scroll-padding-inline-start: var(--ym-spacer-size-xxxs);
    gap: var(--ym-spacer-size-xs);

    height: 100%;
    min-height: 48px;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        margin-inline-start: calc(-1 * var(--ym-spacer-size-l));
        margin-inline-end: calc(-1 * var(--ym-spacer-size-l));
        padding-inline-start: var(--ym-spacer-size-l);
        scroll-padding-inline-start: var(--ym-spacer-size-l);
    }
}

.filter {
    flex-shrink: 0;

    box-sizing: border-box;
    border: 2px solid var(--ym-controls-color-primary-outline-enabled);
    border-radius: var(--ym-radius-size-xxxl);
    background-color: var(--ym-controls-color-primary-outline-enabled);

    max-width: 200px;
    height: 100%;
    scroll-snap-align: start;
    list-style-type: none;

    &_selected {
        border-color: var(--ym-controls-color-primary-default-enabled);
    }
}

.compilations {
    padding-inline-start: var(--ym-spacer-size-xxxl);
    padding-inline-end: var(--ym-spacer-size-xxxl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-inline-start: var(--ym-spacer-size-l);
        padding-inline-end: var(--ym-spacer-size-l);
    }
}

.footer {
    padding-block-end: var(--ym-spacer-size-xxl);
    padding-inline-start: var(--ym-spacer-size-xxxl);
    padding-inline-end: var(--ym-spacer-size-xxxl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-block-end: var(--ym-spacer-size-xxxl);
        padding-inline-start: var(--ym-spacer-size-l);
        padding-inline-end: var(--ym-spacer-size-l);
    }
}
